.content__block {
  display: grid;
  grid-template: 1fr / minmax(auto, 400px) minmax(auto, 1000px);
}

.content__menu {
  grid-column: 1/2;
}

.content__description {
  grid-column: 2/3;
}


.content__menu a:hover{
  color: turquoise;
}

a.menu__item_activeLink{
  color: turquoise;
}

